<template>
  <section class="content" v-if="weather">
    <div class="container">
      <div class="content__left">
        <p class="content__left-temp">
          {{ Math.round(weather.current.temp) }}°
        </p>
        <p class="content__left-day">Сегодня</p>
        <p class="content__left-time">Время: {{ getTime }}</p>
        <p class="content__left-city">Город: {{ weather.timezone }}</p>
        <img class="content__left-img" :src="getIcon" alt="" />
      </div>

      <div class="content__right">
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/1.svg" alt="" />
          </div>
          <p class="content__right-item-secr">
            Температура
            <span
              >{{ Math.round(weather.current.temp) }}° - ощущается как
              {{ Math.round(weather.current.feels_like) }}°</span
            >
          </p>
        </div>
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/2.svg" alt="" />
          </div>
          <p class="content__right-item-secr">
            Давление
            <span
              >{{ getPress }} мм ртутного столба - {{ this.a }}
              {{ getLevelPress }}</span
            >
          </p>
        </div>
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/3.svg" alt="" />
          </div>
          <p class="content__right-item-secr">
            Осадки
            <span>Вероятность осадков - {{ weather.current.humidity }}%</span>
          </p>
        </div>
        <div class="content__right-item">
          <div class="content__right-item-img">
            <img src="@/assets/img/4.svg" alt="" />
          </div>
          <p class="content__right-item-secr">
            Ветер
            <span
              >{{ Math.round(weather.current.wind_speed) }} м/с {{ this.deg }}
              {{ getDegWind }} - {{ this.speed }} {{ getSpeedWind }}
            </span>
          </p>
        </div>
        <img src="@/assets/img/cloud.svg" alt="" class="content__right-bg" />
      </div>
    </div>
  </section>

  <div class="loader" v-else>
    <span></span>
    <span></span>
    <span></span>
    <span></span>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      a: null,
      deg: null,
      speed: null,
    };
  },
  computed: {
    ...mapState(["weather"]),
    getTime() {
      return new Date().toLocaleString("ru-RU", {
        timeZone: this.weather.current.timezone,
        timeStyle: "short",
      });
    },
    getIcon() {
      return `https://openweathermap.org/img/wn/${this.weather.current.weather[0].icon}@2x.png`;
    },
    getPress() {
      let mmHg = this.weather.current.pressure * 0.750062;
      return Math.round(mmHg);
    },
    getLevelPress() {
      if (this.getPress > 765) {
        this.a = "Высокое давление";
      } else if (this.getPress < 755) {
        this.a = "Низкое давление";
      } else {
        this.a = "Нормальное давление";
      }
      return;
    },
    getDegWind() {
      const WindDeg = this.weather.current.wind_deg;

      if (WindDeg >= 337.5 || WindDeg < 22.5) {
        this.deg = "Северный ветер";
      } else if (WindDeg >= 22.5 && WindDeg < 67.5) {
        this.deg = "Северо-восточный ветер";
      } else if (WindDeg >= 67.5 && WindDeg < 112.5) {
        this.deg = "Восточный ветер";
      } else if (WindDeg >= 112.5 && WindDeg < 157.5) {
        this.deg = "Юго-восточный ветер";
      } else if (WindDeg >= 157.5 && WindDeg < 202.5) {
        this.deg = "Южный ветер";
      } else if (WindDeg >= 202.5 && WindDeg < 247.5) {
        this.deg = "Юго-западный ветер";
      } else if (WindDeg >= 247.5 && WindDeg < 292.5) {
        this.deg = "Западный ветер";
      } else if (WindDeg >= 292.5 && WindDeg < 337.5) {
        this.deg = "Северо-западный ветер";
      }
      return;
    },
    getSpeedWind() {
      const WindSpeed = Math.round(this.weather.current.wind_speed);

      if (WindSpeed >= 0 && WindSpeed <= 0.2) {
        this.speed = "Штиль";
      } else if (WindSpeed >= 0.3 && WindSpeed <= 1.5) {
        this.speed = "Тихий ветер";
      } else if (WindSpeed >= 1.6 && WindSpeed <= 3.3) {
        this.speed = "Легкий ветер";
      } else if (WindSpeed >= 3.4 && WindSpeed <= 5.4) {
        this.speed = "Слабый ветер";
      } else if (WindSpeed >= 5.5 && WindSpeed <= 7.9) {
        this.speed = "Умеренный ветер";
      } else if (WindSpeed >= 8 && WindSpeed <= 10.7) {
        this.speed = "Свежий ветер";
      } else if (WindSpeed >= 10.8 && WindSpeed <= 13.8) {
        this.speed = "Cильный ветер";
      } else if (WindSpeed >= 13.9 && WindSpeed <= 17.1) {
        this.speed = "Крепкий ветер";
      } else if (WindSpeed >= 17.2 && WindSpeed <= 20.7) {
        this.speed = "Очень крепкий ветер";
      } else if (WindSpeed >= 20.8 && WindSpeed <= 24.4) {
        this.speed = "Шторм";
      } else if (WindSpeed >= 24.5 && WindSpeed <= 28.4) {
        this.speed = "Сильный шторм";
      } else if (WindSpeed >= 28.5) {
        this.speed = "Ураган";
      }
      return;
    },
  },
};
</script>

<style lang="scss" scoped>
.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  height: 96px;
  width: 96px;
  animation: rotate_3922 1.2s linear infinite;
  background-color: #9b59b6;
  background-image: linear-gradient(#9b59b6, #84cdfa, #5ad1cd);
}

.loader span {
  position: absolute;
  border-radius: 50%;
  height: 100%;
  width: 100%;
  background-color: #9b59b6;
  background-image: linear-gradient(#9b59b6, #84cdfa, #5ad1cd);
}

.loader span:nth-of-type(1) {
  filter: blur(5px);
}

.loader span:nth-of-type(2) {
  filter: blur(10px);
}

.loader span:nth-of-type(3) {
  filter: blur(25px);
}

.loader span:nth-of-type(4) {
  filter: blur(50px);
}

.loader::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  background-color: #fff;
  border: solid 5px #ffffff;
  border-radius: 50%;
}

@keyframes rotate_3922 {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
</style>